//==================================================
//=== Position ==== == = -- -
//==================================================

.c\:pos-unset{
    position: unset;
}
.c\:pos-relative{
    position: relative;
}
.c\:pos-fixed{
    position: fixed;
}
.c\:pos-absolute{
    position: absolute;
}

.c\:pos-0{
    top:    0;
    right:  0;
    bottom: 0;
    left:   0;
}
.c\:posy-0{
    top:    0;
    bottom: 0;
}
.c\:posx-0{
    right:  0;
    left:   0;
}
.c\:posl-0{
    left:   0;
}
.c\:posr-0{
    right:  0;
}
.c\:post-0{
    top:    0;
}
.c\:posb-0{
    bottom: 0;
}

.c\:post-100per{
    top: 100%;
}
.c\:post-n100per{
    top: -100%;
}
.c\:posr-100per{
    right: 100%;
}
.c\:posr-n100per{
    right: -100%;
}
.c\:posl-100per{
    right: 100%;
}
.c\:posl-n100per{
    right: -100%;
}
.c\:posb-100per{
    bottom: 100%;
}
.c\:posb-n100per{
    bottom: -100%;
}




//==================================================
//=== Display ==== == = -- -
//==================================================
.c\:d-grid{
    display: grid;
}
.c\:d-hide{
    display:none;
}
.c\:d-flex{
    display:flex;
}
.c\:d-block{
    display:block;
}
.c\:d-inline-block{
    display:inline-block;
}




//==================================================
//=== Header Font ==== == = -- -
//==================================================

$titles-font: ("6": "3.5", "7": "3", "8": "2.75", "9": "2.25");
@each $key , $value  in $titles-font {
    .c\:h#{$key} {
        font-size: #{$value}rem;
    }
}


//==================================================
//=== Margin & Padding ==== == = -- -
//==================================================
.c\:mt-6 {
    margin-top: 3.5rem;
}
.c\:mt-7 {
    margin-top: 4rem;
}
.c\:mt-8 {
    margin-top: 4.5rem;
}
.c\:mt-9 {
    margin-top: 5rem;
}
.c\:mt-big {
    margin-top: 10rem;
}

.c\:mb-6 {
    margin-bottom: 3.5rem;
}
.c\:mb-7 {
    margin-bottom: 4rem;
}
.c\:mb-8 {
    margin-bottom: 4.5rem;
}
.c\:mb-9 {
    margin-bottom: 5rem;
}

.c\:mb-big {
    margin-bottom: 10rem;
}
@for $i from 1 through 25 {
    $num : $i * 5;
    $neg_num : $i * -5;
    .c\:p-#{$num}x{
        padding-top:    #{$num}px;
        padding-right:  #{$num}px;
        padding-bottom: #{$num}px;
        padding-left:   #{$num}px;
    }
    .c\:py-#{$num}x{
        padding-top:    #{$num}px;
        padding-bottom: #{$num}px;
    }
    .c\:px-#{$num}x{
        padding-right:  #{$num}px;
        padding-left:   #{$num}px;
    }
    .c\:pl-#{$num}x{
        padding-left:   #{$num}px;
    }
    .c\:pr-#{$num}x{
        padding-right:  #{$num}px;
    }
    .c\:pt-#{$num}x{
        padding-top:    #{$num}px;
    }
    .c\:pb-#{$num}x{
        padding-bottom: #{$num}px;
    }
    .c\:pl-n#{$num}x{
        padding-left:   #{$neg_num}px;
    }
    .c\:pr-n#{$num}x{
        padding-right:  #{$neg_num}px;
    }
    .c\:pt-n#{$num}x{
        padding-top:    #{$neg_num}px;
    }
    .c\:pb-n#{$num}x{
        padding-bottom: #{$neg_num}px;
    }

    .c\:m-#{$num}x{
        margin-top:    #{$num}px;
        margin-right:  #{$num}px;
        margin-bottom: #{$num}px;
        margin-left:   #{$num}px;
    }
    .c\:my-#{$num}x{
        margin-top:    #{$num}px;
        margin-bottom: #{$num}px;
    }
    .c\:mx-#{$num}x{
        margin-right:  #{$num}px;
        margin-left:   #{$num}px;
    }
    .c\:ml-#{$num}x{
        margin-left:   #{$num}px;
    }
    .c\:mr-#{$num}x{
        margin-right:  #{$num}px;
    }
    .c\:mt-#{$num}x{
        margin-top:    #{$num}px;
    }
    .c\:mb-#{$num}x{
        margin-bottom: #{$num}px;
    }

    .c\:ml-n#{$num}x{
        margin-left:   #{$neg_num}px;
    }
    .c\:mr-n#{$num}x{
        margin-right:  #{$neg_num}px;
    }
    .c\:mt-n#{$num}x{
        margin-top:    #{$neg_num}px;
    }
    .c\:mb-n#{$num}x{
        margin-bottom: #{$neg_num}px;
    }

    .c\:pos-#{$num}x{
        top:    #{$num}px;
        right:  #{$num}px;
        bottom: #{$num}px;
        left:   #{$num}px;
    }
    .c\:posy-#{$num}x{
        top:    #{$num}px;
        bottom: #{$num}px;
    }
    .c\:posx-#{$num}x{
        right:  #{$num}px;
        left:   #{$num}px;
    }
    .c\:posl-#{$num}x{
        left:   #{$num}px;
    }
    .c\:posr-#{$num}x{
        right:  #{$num}px;
    }
    .c\:post-#{$num}x{
        top:    #{$num}px;
    }
    .c\:posb-#{$num}x{
        bottom: #{$num}px;
    }

    .c\:posl-n#{$num}x{
        left:   #{$neg_num}px;
    }
    .c\:posr-n#{$num}x{
        right:  #{$neg_num}px;
    }
    .c\:post-n#{$num}x{
        top:    #{$neg_num}px;
    }
    .c\:posb-n#{$num}x{
        bottom: #{$neg_num}px;
    }

}





//==================================================
//=== Radius ==== == = -- -
//==================================================
.c\:rounded-unset{
    border-radius:unset!important;
}
@for $i from 1 through 20 {
    $r : ($i * 5);
    $per : percentage($i * 5)/100;

    .c\:rounded-#{$r}x{
        border-radius:#{$r}px;
    }
    .c\:rounded-#{$r}\%{
        border-radius:#{$per};
    }


    .c\:rounded-cw-#{$r}x{
        border-radius: #{$r}px 0;
    }
    .c\:rounded-ccw-#{$r}x{
        border-radius: 0 #{$r}px;
    }
    .c\:rounded-r-#{$r}x{
        border-radius: 0 #{$r}px #{$r}px 0;
    }
    .c\:rounded-l-#{$r}x{
        border-radius: #{$r}px 0 0 #{$r}px;
    }
    .c\:rounded-b-#{$r}x{
        border-radius: 0 0 #{$r}px #{$r}px;
    }
    .c\:rounded-t-#{$r}x{
        border-radius: #{$r}px #{$r}px 0 0;
    }

}




//==================================================
//=== Line Height ==== == = -- -
//==================================================
@for $i from 18 through 45 {
    $h : $i;
    .c\:line-h-#{$h}x{
        line-height: #{$h}px;
    }
}





///*==================================================
//=== Cursor ==== == = -- -
//==================================================*/
.c\:url{
    /*cursor:url(cursor.cur),auto;*/
}
$cursors: none, default, grab, pointer, pointer, not-allowed, wait;
@each $cursor in $cursors {
    .c\:cursor-#{$cursor} {
        cursor: #{$cursor};
    }
}






///*==================================================
//=== Z-index ==== == = -- -
//==================================================*/
.c\:z-on{
    z-index:99999999999999999999999999999999999999999999999999999999;
}
@for $i from -1 through 100 {
    $z: $i * 10;
    .c\:z-#{$z} {
        z-index: #{$z};
    }
}





//==================================================
//=== Width ==== == = -- -
//==================================================
.c\:w-0{
    width: 0;
}
.c\:mw-content{
    max-width: max-content;
}
.c\:mw-content\!{
    max-width: max-content!important;
}
.c\:w-content{
    width: max-content;
}
.c\:w-content\!{
    width: max-content!important;
}
.c\:w-fit{
    width: fit-content;
}
@for $i from 1 through 50 {//pixel
    $w : $i * 10;
    .c\:w-#{$w}x {
        width: #{$w}px;
    }
    .c\:mw-#{$w}x {
        max-width: #{$w}px;
    }
}
@for $i from 1 through 20 {//Percent
    $name : $i * 5;
    $value : percentage($i * 5)/100;
    .c\:w-#{$name} {
        max-width: #{$value};
    }
    .c\:mw-#{$name} {
        max-width: #{$value};
    }
}



///*==================================================
//=== Height ==== == = -- -
//==================================================*/
.c\:h-0{
    height: 0;
}
.c\:h-content{
    max-height: max-content;
}
.c\:maxh-content\!{
    max-height: max-content!important;
}
.c\:h-content{
    height: max-content;
}
.c\:h-content-imp{
    height: max-content!important;
}
.c\:h-fit{
    height: fit-content;
}
.c\:h-calc40{
    height: calc(100% - 40px);
}
.c\:h-calc50{
    height: calc(100% - 50px);
}
@for $i from 1 through 50 {//pixel
    $h : $i * 10;
    .c\:h-#{$h}x {
        height: #{$h}px;
    }
    .c\:mh-#{$h}x {
        max-height: #{$h}px;
    }
}
@for $i from 1 through 20 {//percent
    $h : $i * 5;
    $per : percentage($i * 5)/100;

    .c\:h-#{$h} {
        height: #{$per};
    }
    .c\:mh-#{$h} {
        max-height: #{$per};
    }
}




///*==================================================
//=== Font ==== == = -- -
//==================================================*/
@for $i from 8 through 60 {
    .c\:f-#{$i}x {
        font-size: #{$i}px;
    }
}




///*==================================================
//=== Opacity ==== == = -- -
//==================================================*/
@for $i from 0 through 10 {
    $op : $i * 0.1;
    .c\:op-#{$i}{
        opacity: #{$op};
    }
}




///*==================================================
//=== Limit Text ==== == = -- -
//==================================================*/
@for $i from 0 through 5 {
    .c\:limitText-#{$i}{
        display: -webkit-box;
        -webkit-line-clamp: #{$i};
        -webkit-box-orient: vertical;
        overflow: hidden;
    }
}


///*==================================================
//=== Letter Spacing ==== == = -- -
//==================================================*/
@for $i from 1 through 10 {
    .c\:letter-sp-#{$i}x{
        letter-spacing: #{$i}px;
    }
}





//==================================================
//=== Border ==== == = -- -
//==================================================
@for $i from 1 through 3 {

    .c\:border-#{$i}{
        border: #{$i}px solid;
    }

    .c\:border-y-#{$i}{
        border-top: #{$i}px solid;
        border-bottom: #{$i}px solid;
    }
    .c\:border-x-#{$i}{
        border-left: #{$i}px solid;
        border-right: #{$i}px solid;
    }

    .c\:border-t-#{$i}{
        border-top: #{$i}px solid;
    }
    .c\:border-b-#{$i}{
        border-bottom: #{$i}px solid;
    }
    .c\:border-r-#{$i}{
        border-right: #{$i}px solid;
    }
    .c\:border-l-#{$i}{
        border-left: #{$i}px solid;
    }

}




//==================================================
//=== Label ==== == = -- -
//==================================================
$labels: ("danger": #d9534f,);
@each $key , $value in $labels {
    .c\:label-#{$key} {
        color: #{$value};
        background-color: unset;
        border: 1px solid #{$value};
    }
}
