/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*-----------------------------------------------------------------------*/
/*==================================================
=== Scroll ==== == = -- -
==================================================*/
::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar:hover {
  opacity: 1;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #4b4b4b;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #333;
}

/*==================================================
===  ==== == = -- -
==================================================*/
img, button, a {
  -webkit-user-select: none;
  -webkit-user-drag: none;
  -webkit-app-region: no-drag;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/*==================================================
=== Inputs ==== == = -- -
==================================================*/
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number], input[type=search] {
  -moz-appearance: textfield;
}

/*==================================================
=== Scroll ==== == = -- -
==================================================*/
.scroll-trans::-webkit-scrollbar {
  width: 1px;
}

.scroll-trans::-webkit-scrollbar-track {
  background: transparent;
}

.scroll-trans::-webkit-scrollbar-thumb {
  background: transparent;
}

.scroll-trans::-webkit-scrollbar-thumb:hover {
  background: transparent;
}

.scroll-y-auto {
  overflow-y: auto;
}

.scroll-y-hide {
  overflow-y: hidden;
}

.scroll-x-hide {
  overflow-x: hidden;
}

/*==================================================
=== Unordered List ==== == = -- -
==================================================*/
.unstyled li {
  list-style: none;
}

/*==================================================
=== Buttons ==== == = -- -
==================================================*/
.no-btn {
  background: unset !important;
  border: unset !important;
  color: inherit !important;
}

/*==================================================
=== Table ==== == = -- -
==================================================*/
.center-table {
  display: table-cell;
  vertical-align: middle;
}

/*==================================================
=== Box Shadow ==== == = -- -
==================================================*/
.box-shadow {
  box-shadow: 0 0 21px 4px #f2f2f2;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
}

.box-shadow-commerce {
  /*box-shadow: 0 0 21px 4px rgb(242 242 242);*/
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
}

/*==================================================
=== Text ==== == = -- -
==================================================*/
.text-flow\:hidden {
  text-overflow: ellipsis;
}

.text-lastline-center {
  text-align-last: center;
}

.text-lastline-right {
  text-align-last: right;
}

.nextline {
  white-space: initial;
  word-wrap: break-word;
}

/*==================================================
=== Overflow ==== == = -- -
==================================================*/
.overHidden {
  overflow: hidden;
}

/*==================================================
=== Others ==== == = -- -
==================================================*/
.selectable-none, [data-selectable=false] {
  -webkit-user-select: none;
  -webkit-user-drag: none;
  -webkit-app-region: no-drag;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

